<template>
<!--      -->
<div class="save">
    <div class ='header'>
        <h1 id="head"> </h1>
        <!-- trashcan button:       -->
        <button id="trashcan" v-on:click="del_a"></button> 
    </div>

    <!--      -->
    <div class='contents'>
        <div v-if="noResult==true" class="noResult">
            <br />
            <p>  .</p><p>       </p>
        </div>

        <div v-for="(course, key) in this.courses" :key="key" class="content">
            
             <div class="section1">
                <p>{{course.name}}{{`[${course.professor}]`}} </p>
                <p class="code">{{course.code}} </p>
             </div>


            <span class="section2">
                <p>{{course.gubun}}</p>
                <p>{{course.credit}}</p>
            </span>

            <span class="section3">
                <p>{{course.time}}</p>
                <p> {{course.english}}</p>
            </span>

            <span  class="section4">
                <button id="delete" v-on:click="del(key)"></button>
                <br/>
                <button id="add" v-on:click="add(key)"></button>
            </span>

            <hr />
        </div>
    </div>

    <!--       -->
    <h1 id="foot">
        <button class="all" v-on:click="add_a()"></button>
     <!-- <button class="all" v-on:click="del_a()"></button> -->
    </h1>    
</div>
   
</template>

<script>
import subjects from '../timetable.json'
   
export default{
    data(){
        return{
            active: false,
            courses : [],
            noResult : true
        }
            
    },
    methods : {
        //   
        del(key) {
            this.$http.post('/api/make/del_fav', {
                student_id : this.$session.get('student_id'),
                code : this.courses[key].code
            })
            this.courses.splice(key, 1);
        },
        //   
        add(key){
            this.$EventBus.$emit('courses',this.courses[key]);
        },
        //    
        del_a(){
            var empty = this.$confirm('    ?')
            .then((empty) => {
                if(empty) {
                    this.$http.post('/api/make/del_all_fav', {
                    student_id : this.$session.get('student_id'),
                    });
                this.courses.splice(0, (this.courses.length));
                }
            });
        },
        //     
        add_a(){
            this.$EventBus.$emit('add_a',this.courses);
        },
        //    
        add_to_fav(course) {
            var duplication = this.duplication(course);
            this.noResult = false;
            // console.log("created: " + course);
            
            if(duplication === true) {
                //popup
                this.$notify({
                    group: 'foo',
                    title: '!',
                    text: '   .',
                    duration: 400,
                    type: 'warn',
                });
            } else {
                this.$http.post('/api/make/add_fav', {
                    student_id : this.$session.get('student_id'),
                    code : course.code,
                    course_name : course.name,
                    professor : course.professor,
                    time : course.time,
                    credit : course.credit
                });
                this.courses.push({
                    name: course.name,
                    code: course.code,
                    time: course.time,
                    credit: course.credit,
                    gubun: course.gubun,
                    professor: course.professor,
                    english: course.english
                });
            }
        },
        // 
        duplication(course) {
            var duplication = false;
            for(var i in this.courses) {
                if(course.code === this.courses[i].code) {
                    duplication = true;
                    break;
                }
            }
            return duplication;
        }
    },
    created(){
        this.$http.post('/api/make/fav_list', {
            student_id : this.$session.get('student_id')
        }).then((response) => {
            if (response.status === 200) {
                if(response.data.length == 0){
                    this.noResult = true;
                }else{
                    this.noResult = false;
                    this.courses = response.data; //name, code, time, credit, gubun, professor, english
                }
               
            }
        },function(err){
            alert(" . histime206@gmail.com    :) ")
        });
        this.$EventBus.$on('add_to_fav', this.add_to_fav);
    }
}
 
    
</script>


<style  lang ="scss" src = '../../assets/Makepage/save.scss' scoped>
</style>


