<template>
 <div class="main">
       <h1 id="head"></h1> 
       <!-- -->
       
       <div class="searchBox" > 
             <!-- -->
              <span v-if ="!this.showbox">
            <input v-model="course_name" type="text" placeholder="    " class='input_text' name="search"  v-on:keydown.enter="search_by_name" />
            <input type="button" class='sch_filt' value="" v-on:click="search_by_name"/>            
            </span>

           <!-- -->
           <span v-if ="this.showbox">
            <input v-model="course_name" type="text" placeholder="    " class='input_text' name="search"  disabled="disabled" v-on:keydown.enter="search_by_name" />
            <input type="button" class='sch_filt' value="" v-on:click="search_by_Filter"/>
            </span>
          

            <input type="button" class='sch_filt' value="" v-on:click="show"/>
       </div>
 

  <div class='contents'>
      <p id="noResult" v-if='no_result === true'> .</p><!--  -->
        <p id="loading" v-if='loading=== true'>...</p><!--  -->
        <div  v-for="(course, key) in search" :key= "key">
            <div class="content" >
                        <div class="section1"> 
                            <!--   -->
                            <p>{{course.name}}{{`  [${course.professor}]`}} </p>
                            <p class = "code"> {{course.code}}</p>
                        </div>
                       

                        <span class="section2">
                            <p>{{course.gubun}}</p> 
                            <!--   ( ),,-->
                            <p>{{course.credit}}</p>
                        </span>

                        <span class="section3">
                            <!--  , -->
                            <p>{{course.time}}</p>
                            <p> {{course.english}}</p>
                        </span>
                         <span class="section4"> 
                            <!--      -->
                            <button id="fav" v-on:click="(event) => { add_to_fav(key) }"></button>
                            <br/>
                            <button id="add" v-on:click="(event) => { add_to_tt(key) }"></button>
                        </span>
                        
                        <hr />
                       
             </div>
               
      </div>
        
    </div>
    
      <div v-show="showbox" class="placeholder-box" >  <!--    -->
            <p>&emsp;&ensp;&nbsp;  <!--selectbox   -->
                <select v-model="filter.hakbu">
                    <option value=""></option>
                    <option v-for="course_name in course_names" :key="course_name" > 
                        {{course_name}} 
                    </option>
                </select>
            </p>
            <p>  <!--selectbox   -->
                <select v-model="filter.gubun">
                    <option value=""></option>
                    <option  v-for=" gubun in gubuns " :key="gubun">
                        {{ gubun }}
                    </option>
                </select>
            </p>
            <p> <!--selectbox   -->
                <select v-model="filter.gyoyang">
                        <option value=""></option>
                        <option v-for=" gyoyang in gyoyangs" :key="gyoyang">
                            {{gyoyang}}
                        </option>
                </select>
            </p>
            <p> &ensp;&nbsp; <!--selectbox   -->
            <input v-model="filter.professor" type="text" class="pfname" placeholder="  "/>
            </p>     

            <p>&emsp;&ensp;&nbsp;<!--selectbox   -->
                    <input type="checkbox" id="credit" value="0.5" checked="false" v-model="filter.credit[0]">
                    <label for="credit">&nbsp;0.5</label>
                    <input type="checkbox" id="credit1" value="1" checked="false" v-model="filter.credit[1]">
                    <label for="credit1">&ensp;&nbsp;1</label>
                    <input type="checkbox" id="credit2" value="2" checked="false" v-model="filter.credit[2]">
                    <label for="credit2">&ensp;&nbsp;2</label>
                    <input type="checkbox" id="credit3" value="3" checked="false" v-model="filter.credit[3]">
                    <label for="credit3">&ensp;&nbsp;3</label>
                    <input type="checkbox" id="credit4" value="4" checked="false" v-model="filter.credit[4]">
                    <label for="credit4">&ensp;&nbsp;4</label>
            </p>

            
            <p> <!--selectbox   -->
            <select v-model="filter.english">
                        <option value=""></option>
                        <option v-for=" english in englishs" :key="english">
                            {{english}}
                        </option>
            </select>
            </p>

        
                <p> &nbsp;&ensp;  <!--selectbox   -->
               
                <input type="button" class='openchoose' value=" " v-on:click="show2"/> 
                <!--   placeholderbox  -->
                 
                </p>

         
           <center><input type="button"  class="search" value="" v-on:click="search_by_Filter"></center>

            <div v-show="searchbox" class="timesearchBox">
                <p>    </p> 
                <!-- Database         -->
                         <table>
                            <thead>
                                <tr>
                                 <th></th><th></th><th></th><th></th><th></th><th></th><th></th>
                                </tr>
                            </thead>
                            <tbody> <!-- -->
                                <tr v-for ="i in 11" :key ="i">
                                    <td>{{ i }}</td>
                                    <td  v-for ="j in 6" :key ="j" > 
                                        <input type="checkbox" id="checktime" value="">
                                        <label for="checktime"  v-on:click="checktimef(i,j)" v-bind:class="{checked : checktime[(i+j*11)]} " ></label>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                     
                
                <p> <input type="button" class="choosedone" value=" done "  v-on:click="timebox_chosen">
                    <!--    -->
                    <input type="button" class="choosereset" value=" reset " v-on:click="timebox_reset" >
                    <!--     -->
                </p>
            </div>
       </div>
       <div id="foot">
           <!-- user            -->
            <span id="update">    : {{this.update}}</span>
       </div>
    </div>
  
</template>

<script type="text/javascript">
export default {
    data(){
        return{
            // frontend  
            course_names:['','','','','','','','','','',
            '()','()','()','','ICT'],
            gubuns: ['','','','','','',''],
            gyoyangs:['1','3','1','2','','2','1','2',
            '1','','','','1','2','','','',
            'ICT','','','','','',''],
            englishs:['100%','0%'],
            course_name: '',
            showbox:false,
            searchbox:false,
            checkflag:false,
            search:[], 
            checktime:[],
            filter:{
                hakbu:'',
                gubun:'',
                gyoyang:'',
                professor:'',
                english:'',
                time:[],
                credit:[]
            },
            no_result : true,//   
            loading : false,//    
            update : ''
        
            
        }
    },
    created(){
        this.get_update_time();
    },
  

    
    methods:{
        show: function(){
            this.course_name = '';//  
            this.showbox=!this.showbox; // showbox 1- 2-  
            this.filter.credit.splice(false, this.filter.credit.length);        


        },
        show2: function(){
        //searchbox 1- 2-
            this.searchbox=!this.searchbox;
           
            
        },
        timebox_chosen: function(){
            // console.log(this.checktime);
            this.searchbox=false;
        },
        timebox_reset:function(){
            // console.log('into timebox reset');
            this.checktime.splice(0, this.checktime.length);        
        },
        creditslt:function(){
           this.style.background='powderblue';

        },
        search_by_name: function(){//,    
            if(this.course_name.length==''){
                return;
            } else{
                this.no_result = false;//' '   
                this.search=''// 
                this.loading = true;//'..' 
                this.$http.post('/api/make/search/name', {
                course_name : this.course_name,
                }).then((response) => {
                    this.loading = false //'' 
                    if(response.data.length==0){//   
                        this.no_result = true;//  
                    }else{
                        this.no_result = false;
                        this.search = response.data;// 
                    }
                },function (err) {//  
                    
                    alert(" . histime206@gmail.com    :) ")
                });
            }
        },
        //  ,     ,
        //       
        checktimef:function(i,j){
            this.checktime[(i + j * 11)] = !this.checktime[(i + j * 11)];
            this.$forceUpdate()

        },
       
        search_by_Filter: function(){
            for(var i=1;i<78; i++) {
                    if(this.checktime[i] === undefined)continue;
                 if(this.checktime[i]===true) {
                    // selectbox2      
                    // console.log('j' + j);
                    var day = ''; 
                    var time = i % 11;
                    var j = (i - time)/11;
                    if(j === 1) day +=  '';
                    else if(j === 2) day += '';
                    else if(j === 3) day += '';
                    else if(j === 4) day += '';
                    else if(j === 5) day += '';
                    else if(j === 6) day += '';
                    // console.log(`${day}${time}`);

                    this.filter.time.push(`${day}${time}`);
                    
                }         //this.checktime[i][j]===false   done  
            }
            this.no_result = false;//' '   
            this.search=''// 
            this.loading = true;//'..' 
            
            this.$http.post('/api/make/search/filter', {
                hakbu : this.filter.hakbu,
                gubun : this.filter.gubun,
                gyoyang : this.filter.gyoyang,
                credit : this.filter.credit,
                english : this.filter.english,
                professor : this.filter.professor,
                time : this.filter.time,
                 }).then((response) => {
                    this.loading = false //'' 
                    if(response.data.length==0){//   
                        this.no_result = true;//  
                    }else{
                        this.no_result = false;
                        this.search = response.data;// 
                    }
                },function (err) {//  
                    alert(" . histime206@gmail.com    :) ")
                });

            // 
            this.filter.hakbu = '';
            this.filter.gubun = '';
            this.filter.gyoyang = '';
            this.filter.credit = [];
            this.filter.english = '';
            this.filter.professor = '';
            this.filter.time = [];

            // 
            this.checktime = [];
            this.filter.credit = [];
            this.showbox  = !this.showbox;
        },
         add_to_fav: function(key){
            // console.log(key);
            // console.log(this.search[key].name);
            this.$EventBus.$emit('add_to_fav',this.search[key]);
        },
        add_to_tt: function(key){
            // console.log(key);

            this.$EventBus.$emit('courses', this.search[key]);
            // this.$http.post('/api/make/add_fav', {
            //     student_id : this.$session.get('student_id'),
            //     code : this.search[key].code,
            //     course_name : this.search[key].name,
            //     professor : this.search[key].professor,
            //     time : this.search[key].time,
            //     credit : this.search[key].credit
            //     }).then((response) => {
            //         console.log(response.data);
            //         search = response.data;
            // });
        },
        get_update_time(){
            this.$http.get('/api/make/update_time').then((response) => {
                this.update = response.data[0].time;
        });
        }
    }
}

</script>
<style   src = '../../assets/Makepage/search.scss' lang = "scss" scoped>
</style>


