
<template>
    <div>
    <!--for demo wrap-->
    <h1> </h1>
    <div class="tbl-header">
        <table cellpadding="0" cellspacing="0" border="0">
        <thead>
                <tr>  
                    <th id="h_index"></th>
                    <th id="h_ttname"> </th>
                    <th id="h_credit"></th>
                    <th id="h_change"></th>
                </tr>
                
        </thead>
        </table>
    </div>

    <div class="tbl-content">
        <p v-if="noResult==true" class="noResult">  .<br />       </p>
        <table cellpadding="0" cellspacing="0" border="0">
        <tbody>
            <div v-for="(ttlist,key) in this.ttlists" :key="key">
                <tr>
                    <td id="index">{{ key+1 }}</td>
                    <td id="namefield">
                        <span v-on:click = " $EventBus.$emit('to_timetables',ttlist.ttname)" id = "ttname">{{ttlist.ttname}}</span>
                        <button id="modify_name" v-on:click="modify_name(key)"></button>
                    </td>
                    <td id="credit">{{ttlist.total_credit}}</td>
                    <td id="edit"><button class ="change" v-on:click="ttedit(key)"></button></td>
                    <td id="del"><button class ="change" v-on:click="ttdelete(key)"></button></td>
                </tr>

                    <hr />
                </div> 
            </tbody>
        </table>
    </div><!--tbl-content ending tag-->
    <div class="add">
            <button id="add" v-on:click="go_make()"> </button>
    </div>
    
    
    
    </div>

    
</template>

<script>
import copy from './copy.vue'


    export default {
        name: 'ttlist',
        //props :['ttlists'],
        components : {
            copy
        },
        data() {
            return {
                ttlists:[],
                ttlist:{
                    ttname :"",
                    ttrank:"",
                    total_credit:""
                },
                ttnames:[],
                noResult : true,
                
            };
        },
        
        created () {
            this.$http.post('/api/show', {
                student_id : this.$session.get('student_id')
            }).then((response) => {
                if (response.status === 200 ) {
                    // console.log(response.data);
                    if(response.data.length === 0){
                        this.noResult = true;
                        return;
                    } 
                    this.noResult =false;
                    this.ttlists = response.data; //ttname, ttrank, total_credit
                    //console.log(this.ttlists);
                    this.$EventBus.$emit('to_timetables',this.ttlists[0].ttname);// show    default    
                }
            });
        },
        methods: {
        
            go_make() { //    
                this.$prompt('   (10)')
                .then((new_ttname) => {
                    if(new_ttname === '') {
                        this.$alert({
                            title: '!',
                            message: '   .',
                            duration: 1000,
                            rbHide: true
                        });
                        this.go_make();
                    } else if(new_ttname === null) {
                        ;
                    }else if(new_ttname.length > 10){
                        this.$alert({
                            title: '!',
                            message: '   .',
                            duration: 1000,
                            rbHide: true
                        });
                        this.go_make();
                    } else if(this.duplication(new_ttname)){
                        this.$alert({
                            title: '!',
                            message: '   .',
                            duration: 1000,
                            rbHide: true
                        });
                        this.go_make();
                    } else {
                        this.$session.set('to_timetablem', new_ttname);//   
                        
                        let routeData = this.$router.resolve({name: 'make'});
                        window.location.href = routeData.href;
                        // this.$router.replace({ name: "make" });
                    }
                });
            },
            modify_name(key) {//  ()-
                var original_ttname = this.ttlists[key].ttname;

                this.$prompt({
                    title : '   ',
                    message : 'Original name : ' + original_ttname,
                })
                .then((new_ttname) => {
                    if(new_ttname === "") {
                        this.$alert({
                            title: '!',
                            message: '   .',
                            duration: 1000,
                            rbHide: true
                        });
                        this.modify_name(key);
                    } else if(new_ttname === null) {
                        ;
                    } else if(new_ttname.length > 10){
                        this.$alert({
                            title: '!',
                            message: '   .',
                            duration: 1000,
                            rbHide: true
                        });
                        this.modify_name(key);
                    }else if(original_ttname === new_ttname) {
                        this.$alert({
                            title: '!',
                            message: '   .',
                            duration: 1000,
                            rbHide: true
                        });
                        this.modify_name(key);
                    } else if(this.duplication(new_ttname)){
                        this.$alert({
                            title: '!',
                            message: '   .',
                            duration: 1000,
                            rbHide: true
                        });
                        this.modify_name(key);
                    } else {//
                        this.$http.post('/api/show/modify_ttname', {
                            student_id :  this.$session.get('student_id'),
                            original_ttname :  original_ttname,
                            modified_ttname : new_ttname
                        });
                        this.ttlists[key].ttname = new_ttname;
                        this.$notify({
                            group: 'foo',
                            text: '  .',
                            duration: 400,
                            type: 'success',
                        });
                    }
                });                
            },
            duplication(new_name){// ,    
            var duplication =   this.ttlists.some(function(item, index, array) {
                                    return (item.ttname === new_name);
                                });
                return duplication;
            },
            ttdelete(key){// 
                var del = this.$confirm('  ?')
                .then((del) => {
                    if(del) {
                        this.$http.post('/api/show/del_tt', {
                        student_id :  this.$session.get('student_id'),
                        ttname :  this.ttlists[key].ttname
                        });
                        this.ttlists.splice(key,1);
                    }
                });                
            },
            ttedit(key){// 
                var edit = this.$confirm(' ?')
                .then((edit) => {
                    if(edit) {
                        this.$session.set('to_timetablem', this.ttlists[key].ttname);//   
                        this.$http.post('/api/show/modify_tt', {
                                student_id :  this.$session.get('student_id'),
                                ttname :  this.ttlists[key].ttname
                        }).then((response) => {
                            var code = [];
                            if (response.status === 200 ) {
                                // console.log(response.data);
                                for(var i = 0; i < response.data.length; i++){
                                    var j = 0;
                                    for(j = 0; j < i; j++){
                                        if(response.data[i].code === response.data[j].code) break;
                                    }
                                    // console.log(response.data[i].code);
                                    if(i === j) code.push(response.data[i].code);
                                }
                                this.$session.set('to_modify', code)
                                this.$router.replace({ name: "make" });
                            }
                        });
                    }
                });
            },
            ttselect(key){
                cur_ttname = this.ttlists[key].ttname;
                // console.log(this.ttnames[key].ttname);
                //console.log(cur_ttname);
                this.$EventBus.$emit('to_timetables',this.ttlists[key].ttname);
            }
        },
    }
    
</script>



<style  src = '../../assets/Showpage/list.scss' lang ="scss" scoped>
</style>


